.van-button {
  width: initial;
  position: relative;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  box-sizing: border-box;
  padding: 0;
  text-align: center;
  vertical-align: middle;
  -webkit-appearance: none;
  -webkit-text-size-adjust: 100%;
  height: 1.87733rem;
  height: var(--button-default-height, 1.87733rem);
  line-height: 0.85333rem;
  line-height: var(--button-line-height, 0.85333rem);
  font-size: 0.68267rem;
  font-size: var(--button-default-font-size, 0.68267rem);
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
  -webkit-transition: opacity var(--animation-duration-fast, 0.2s);
  transition: opacity var(--animation-duration-fast, 0.2s);
  border-radius: 0.08533rem;
  border-radius: var(--button-border-radius, 0.08533rem);
}
.van-button::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  border: inherit;
  border-radius: inherit;
  /* inherit parent's border radius */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0;
  content: ' ';
  background-color: #000;
  background-color: var(--black, #000);
  border-color: #000;
  border-color: var(--black, #000);
}
.van-button::after {
  border-width: 0;
}
.van-button--active:not(.van-button--disabled)::before {
  opacity: 0.15;
}
.van-button--unclickable::after {
  display: none;
}
.van-button--default {
  color: #323233;
  color: var(--button-default-color, #323233);
  background: #fff;
  background: var(--button-default-background-color, #fff);
  border: 0.04267rem solid #ebedf0;
  border: var(--button-border-width, 0.04267rem) solid var(--button-default-border-color, #ebedf0);
}
.van-button--default:not(.van-button--default--disabled):active {
  color: #323233;
  color: var(--button-default-color, #323233);
  background: #fff;
  background: var(--button-default-background-color, #fff);
}
.van-button--primary {
  color: #fff;
  color: var(--button-primary-color, #fff);
  background: #07c160;
  background: var(--button-primary-background-color, #07c160);
  border: 0.04267rem solid #07c160;
  border: var(--button-border-width, 0.04267rem) solid var(--button-primary-border-color, #07c160);
}
.van-button--primary:not(.van-button--primary--disabled):active {
  color: #fff;
  color: var(--button-primary-color, #fff);
  background: #07c160;
  background: var(--button-primary-background-color, #07c160);
}
.van-button--info {
  color: #fff;
