  background: #ee0a24;
  background: var(--button-danger-background-color, #ee0a24);
  border: 0.04267rem solid #ee0a24;
  border: var(--button-border-width, 0.04267rem) solid var(--button-danger-border-color, #ee0a24);
}
.van-button--danger:not(.van-button--danger--disabled):active {
  color: #fff;
