.van-button--hairline::after {
  border-color: inherit;
  border-width: 0.04267rem;
  border-radius: calc(0.08533rem * 2);
  border-radius: calc(var(--button-border-radius, 0.08533rem) * 2);
}
.van-button--hairline.van-button--round::after {
  border-radius: 21.312rem;
  border-radius: var(--button-round-border-radius, 21.312rem);
}
.van-button--hairline.van-button--square::after {
